/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.text {
  color: #7c8092;
  font-size: 16px;
  line-height: 1.875;
}

body {
  background: #FFF;
  font-size: 16px;
  line-height: 1.4;
  font-family: 'Barlow', sans-serif;
}

.out {
  overflow: hidden;
}

.layout:after {
  content: " ";
  display: table;
  clear: both;
}

.layout__left {
  width: 265px;
  float: left;
}

.layout__right {
  overflow: hidden;
  padding-left: 125px;
}

.header__inner {
  padding: 60px 50px 0;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header__logo {
  float: left;
}

.header__right {
  float: right;
}

.header__right:after {
  content: " ";
  display: table;
  clear: both;
}

.header__nav {
  float: left;
  margin-right: 30px;
}

.header__socials {
  float: right;
  padding-top: 8px;
}

.logo__pin {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 24px;
  color: #fff;
  padding: 0 5px;
  background-color: #032c9e;
}

.logo__text {
  color: #fff;
  font-size: 24px;
  text-transform: uppercase;
}

.nav a {
  display: inline-block;
  font-weight: 700;
  color: #fff;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 24px;
  margin: 0 10px;
}

.nav a:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #fff;
  transform: scale(0);
  transition: .3s ease;
  transform-origin: center right;
}

.nav a:hover:before {
  transform: scale(1);
}

.main-banner {
  background-size: cover;
  background-position: center center;
  min-height: 1291px;
  position: relative;
  z-index: 1;
}

.main-banner:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(1, 55, 160, 0.5);
  z-index: -1;
}

.main-banner__wrapper {
  max-width: 920px;
  margin: 0 auto;
  text-align: center;
  padding-top: 270px;
}

.main-banner__subtitle {
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 3px;
}

.main-banner__title {
  color: #fff;
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1;
}

.main-banner__heading {
  margin-bottom: 160px;
}

.main-banner__author {
  max-width: 200px;
  margin: 0 auto;
}

.main-banner__slider .slick-dots {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 30px;
  z-index: 5;
}

.main-banner__slider .slick-dots li {
  padding-right: 75px;
  position: relative;
  cursor: pointer;
}

.main-banner__slider .slick-dots li:not(:last-child) {
  margin-bottom: 5px;
}

.main-banner__slider .slick-dots li.slick-active:before {
  transform: scaleX(1.4);
  background-color: white;
}

.main-banner__slider .slick-dots li:before {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: -1px;
  transform: scaleX(1);
  transform-origin: center right;
  height: 2px;
  background-color: #fff;
  right: 0;
  width: 25px;
  transition: .3s ease;
  background-color: rgba(255, 255, 255, 0.5);
}

.main-banner__slider .slick-dots button {
  background-color: transparent;
  font-weight: 700;
  font-size: 18px;
  color: #fff;
}

.author__image {
  display: inline-block;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
